<template>
  <v-app>
    <v-main>
      <v-container class="pt-12 mt-12 pl-4 pr-4">
        <v-row class="pb-12">
          <v-col cols="12" lg="6" offset-lg="3" class="d-flex flex-column align-center justify-center">
            <span class="goldAccent--text handwriting-text">Ivana Perić</span>
            <span class="goldAccent--text handwriting-text">& Mario Rajič</span>
          </v-col>
        </v-row>
        <v-row>
          <v-col cols="12" class="pa-2">
            <v-timeline>
              <v-timeline-item right icon="mdi-church" class="ma-0">
                <template v-slot:opposite>
                  <v-sheet min-height="150px" max-height="200px" class="d-flex align-center">
                    <v-row no-gutters>

                      <v-col cols="12" class="d-flex flex-column justify-end">
                      <span class="text-h6 font-weight-bold primaryText--text">
                          18:00
                  </span>
                        <span class="font-weight-bold text-caption primary--text">
                          <countdown :time="countdownTimeChurch">
                          <template slot-scope="props">
                            {{ props.days }} dana,
                            {{
                              props.hours
                            }} sati,
                            {{ props.minutes }} minuta,
                            {{ props.seconds }} sekundi
                            .</template>
                        </countdown>
                        </span>


                      </v-col>
                      <v-col cols="12" class="text-end pt-2">
                        <v-btn text tile color="goldAccent" class="text-none"
                               href="https://calendar.google.com/calendar/render?action=TEMPLATE&dates=20210917T160000Z%2F20210917T170000Z&location=crkva%20Presvetog%20Srca%20Isusova%2C%20Potoci&text=Ivana%20i%20Mario%20Crkva">
                          <v-icon small class="mr-2">mdi-calendar</v-icon>
                          Podsjetnik
                        </v-btn>
                      </v-col>
                    </v-row>
                  </v-sheet>

                </template>
                <v-sheet min-height="150px" max-height="200px" class="d-flex align-center">

                  <v-row no-gutters>
                    <v-col cols="12" class="d-flex align-center">
                        <span class="font-weight-bold text-h6 primaryText--text">
                          crkva Presvetog Srca Isusova, Potoci
                       </span>
                    </v-col>
                    <v-col cols="12" class="pt-2">
                      <v-btn text tile color="goldAccent" class="text-none"
                             href="https://www.google.com/maps/place/Crkva+Presvetog+Srca+Isusova/@43.4113608,17.8662265,15z/data=!4m9!1m2!2m1!1scrkva+potoci!3m5!1s0x134b5ac6bc65d1c7:0x433789381f8d32f6!8m2!3d43.4113608!4d17.8749812!15sCgxjcmt2YSBwb3RvY2mSAQ9jYXRob2xpY19jaHVyY2g">
                        <v-icon small class="mr-2">mdi-map-marker</v-icon>
                        Lokacija
                      </v-btn>
                    </v-col>
                  </v-row>
                </v-sheet>


              </v-timeline-item>
              <v-timeline-item right icon="mdi-party-popper">
                <template v-slot:opposite>
                  <v-sheet min-height="150px" max-height="200px" class="d-flex align-center">
                    <v-row no-gutters>
                      <v-col cols="12" class="d-flex flex-column">
                       <span class="text-h6 font-weight-bold primaryText--text">
                          20:00
                  </span>
                        <span class="font-weight-bold text-caption primary--text">
                          <countdown :time="countdownTimeVenera">
                          <template slot-scope="props">
                            {{ props.days }} dana,
                            {{
                              props.hours
                            }} sati,
                            {{ props.minutes }} minuta,
                            {{ props.seconds }} sekundi
                            .</template>
                        </countdown>
                        </span>
                      </v-col>
                      <v-col cols="12" class="text-end pt-2">
                        <v-btn text tile color="goldAccent" class="text-none"
                               href="https://calendar.google.com/calendar/render?action=TEMPLATE&dates=20210917T180000Z%2F20210918T030000Z&location=svadbeni%20salon%20%22Venera%22&text=Ivana%20i%20Mario%20Sala">
                          <v-icon small class="mr-2">mdi-calendar</v-icon>
                          Podsjetnik

                        </v-btn>
                      </v-col>
                    </v-row>
                  </v-sheet>

                </template>
                <v-sheet min-height="150px" max-height="200px" class="d-flex align-center">
                  <v-row no-gutters>
                    <v-col cols="12" class="d-flex flex-column">
                            <span class="font-weight-bold text-h6 primaryText--text">
                              svadbeni salon "Venera"
                           </span>

                    </v-col>
                    <v-col cols="12" class="text-left pt-2">
                      <v-btn text tile color="goldAccent" class="text-none "
                             href="https://www.google.com/maps/place/Svadbeni+salon+Venera/@43.3161623,17.8204841,17z/data=!3m1!4b1!4m5!3m4!1s0x134b42f1ff9f6a87:0x96643c38201081cb!8m2!3d43.3161623!4d17.8226781">
                        <v-icon small class="mr-2">mdi-map-marker</v-icon>
                        Lokacija
                      </v-btn>
                    </v-col>

                  </v-row>
                </v-sheet>
              </v-timeline-item>
            </v-timeline>
          </v-col>

        </v-row>

        <v-row class="pt-12 pb-6">
          <v-col cols="12" class="text-center">
            <v-btn outlined tile color="goldAccent" class="text-none" @click="confirmationDialog = true">
              <v-icon small class="mr-2">mdi-check</v-icon>
              Potvrdi dolazak
            </v-btn>
          </v-col>

          <v-col cols="12" class="text-center pt-2">
            <v-btn outlined tile color="goldAccent" class="text-none" @click="seatsDialog = true">
              <v-icon small class="mr-2">mdi-seat</v-icon>
              Prikaži raspored sjedenja
            </v-btn>
          </v-col>
        </v-row>

      </v-container>

      <v-dialog v-model="seatsDialog" max-width="300px">
        <v-card max-width="300px" class="pa-1">
          <v-card-title>'Ko rano rani...</v-card-title>
          <v-card-text>
            ...morat će se vratit kasnije jer još nismo završili raspored.
          </v-card-text>

          <v-img max-width="300px" src="./assets/yoda.jpg"></v-img>
        </v-card>
      </v-dialog>

      <v-dialog v-model="confirmationDialog" max-width="300px">
        <v-card max-width="300px" class="pa-1">
          <v-card-title>Potvrdite dolazak</v-card-title>
          <v-divider></v-divider>
          <v-card-text>
            <v-form v-model="isConfirmationDataValid" ref="confirmationForm" :lazy-validation="true">
              <v-row no-gutters>
                <v-col cols="12">
                  <v-text-field
                      validate-on-blur
                      label="Ime i prezime"
                      v-model="confirmationForm.fullName"
                      :rules="[v=> v.length > 5 || 'Unesite puno ime i prezime']"
                  >

                  </v-text-field>
                </v-col>
                <v-col cols="12">
                  <v-text-field
                      validate-on-blur
                      type="number"
                      label="Broj telefona"
                      v-model="confirmationForm.phoneNumber"
                      :rules="[
                          v=>
                          (!existingConfirmations.find(c=>c.phoneNumber === v) || 'Dolazak s unesenim brojem je već potvđen'
                          )
                          , v=> (v.toString().length > 5 && v.toString().length <=15 || 'Neispravan broj telefona')]"
                  >

                  </v-text-field>
                </v-col>
                <v-col cols="12">
                  <v-text-field
                      hide-spin-buttons
                      type="number"
                      label="Broj uzvanika"
                      :rules="[v=> (v > 0 && v <=10) || 'Neispravan broj']"
                      v-model="confirmationForm.noOfAttendees"
                      :min="1"
                      :max="10">

                  </v-text-field>
                </v-col>
              </v-row>
            </v-form>
          </v-card-text>
          <v-divider></v-divider>

          <v-card-actions class="d-flex justify-center align-center">
            <v-btn :loading="isConfirmationSaving" text tile color="goldAccent" class="text-none"
                   @click="confirmAttendance">
              Potvrdi
            </v-btn>
          </v-card-actions>
        </v-card>
      </v-dialog>

      <v-snackbar v-model="confirmationSnack" :timeout="4000" centered color="primaryDark" tile>
        <span class="font-weight-bold text-subtitle-1 white--text">
          {{ snackbarText }}
        </span>
        <v-btn icon @click="confirmationSnack = false">
          <v-icon>mdi-close</v-icon>
        </v-btn>
      </v-snackbar>
    </v-main>
  </v-app>
</template>

<script>

import {createConfirmation, db} from "./firebase";

export default {
  name: 'App',

  components: {},
  computed: {
    countdownTimeChurch() {
      return this.$moment.duration(this.$moment('2021-09-17 18:00').diff(this.$moment())).asMilliseconds();
    },
    countdownTimeVenera() {
      return this.$moment.duration(this.$moment('2021-09-17 20:00').diff(this.$moment())).asMilliseconds();
    }
  },

  data: () => ({
    seatsDialog: false,
    confirmationDialog: false,
    confirmationForm: {
      fullName: "",
      phoneNumber: "",
      noOfAttendees: 1,
    },
    isConfirmationSaving: false,
    confirmationSnack: false,
    isConfirmationDataValid: true,
    snackbarText: 'Potvrda spremljena. Radujemo se vašem dolasku!',
    existingConfirmations: []
    //
  }),
  mounted() {
    this.$moment.locale('hr')
  },
  methods: {
    async confirmAttendance() {
      if (this.$refs.confirmationForm.validate()) {
        this.isConfirmationSaving = true;
        await createConfirmation(this.confirmationForm).then(res => {
          if (!res.id) {
            this.snackbarText = 'Nažalost, nismo mogli spremiti vašu potvrdu. Pokušajte kasnije!';
            this.confirmationSnack = true;
          }
          this.snackbarText = 'Potvrda spremljena. Radujemo se vašem dolasku!';
          this.isConfirmationSaving = false;
        });

        this.confirmationForm = {
          fullName: "",
          phoneNumber: "",
          noOfAttendees: 1,
        };
        this.confirmationDialog = false;
        this.confirmationSnack = true;
      }

    }
  },
  created() {
    db.collection('confirmations').onSnapshot(res => {
      const changes = res.docChanges();
      changes.forEach(change => {
        if (change.type === 'added') {
          this.existingConfirmations.push({
            ...change.doc.data()
          })
        }
      })
    })
  }
};
</script>


<style lang="scss">
@import './assets/app.scss';

</style>
